import { Component, OnInit } from '@angular/core';

import { MySelfMobileService } from '../my-self-mobile.service';

@Component({
  selector: 'app-my-self-mobile-pagina',
  templateUrl: './my-self-mobile-pagina.component.html',
  styleUrls: ['./my-self-mobile-pagina.component.scss']
})
export class MySelfMobilePaginaComponent implements OnInit {

  constructor(public mySelfMobileService: MySelfMobileService) { }

  ngOnInit() {
    if (this.mySelfMobileService.pagina == null) {
      this.mySelfMobileService.openPage('pagine');
    } else {
      this.mySelfMobileService.showHeaderBackButton();
    }
  }
}