import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { authGuard, authGuardChild } from '../core/auth/authguard';
import {
  MySelfMobileArticoliRoomServicePrenotazioneComponent,
} from './my-self-mobile-articoli-room-service-prenotazione/my-self-mobile-articoli-room-service-prenotazione.component';
import {
  MySelfMobileArticoliUpSellingPrenotazioneComponent,
} from './my-self-mobile-articoli-up-selling-prenotazione/my-self-mobile-articoli-up-selling-prenotazione.component';
import { MySelfMobileArticoliComponent } from './my-self-mobile-articoli/my-self-mobile-articoli.component';
import { MySelfMobileCheckinComponent } from './my-self-mobile-checkin/my-self-mobile-checkin.component';
import { MySelfMobileClienteEditComponent } from './my-self-mobile-cliente-edit/my-self-mobile-cliente-edit.component';
import { MySelfMobileContenutoComponent } from './my-self-mobile-contenuto/my-self-mobile-contenuto.component';
import { MySelfMobileEventoComponent } from './my-self-mobile-evento/my-self-mobile-evento.component';
import { MySelfMobileHomeComponent } from './my-self-mobile-home/my-self-mobile-home.component';
import {
  MySelfMobileInformazioniStrutturaComponent,
} from './my-self-mobile-informazioni-struttura/my-self-mobile-informazioni-struttura.component';
import { MySelfMobileLoginComponent } from './my-self-mobile-login/my-self-mobile-login.component';
import {
  MySelfMobileMenuGiornoSelectComponent,
} from './my-self-mobile-menu-giorno-select/my-self-mobile-menu-giorno-select.component';
import {
  MySelfMobileMenuPiattiSelectComponent,
} from './my-self-mobile-menu-piatti-select/my-self-mobile-menu-piatti-select.component';
import {
  MySelfMobileMenuPiattiSelectedComponent,
} from './my-self-mobile-menu-piatti-selected/my-self-mobile-menu-piatti-selected.component';
import { MySelfMobileMenuComponent } from './my-self-mobile-menu/my-self-mobile-menu.component';
import { MySelfMobileMessaggiComponent } from './my-self-mobile-messaggi/my-self-mobile-messaggi.component';
import { MySelfMobilePaginaComponent } from './my-self-mobile-pagina/my-self-mobile-pagina.component';
import { MySelfMobilePagineComponent } from './my-self-mobile-pagine/my-self-mobile-pagine.component';
import { MySelfMobilePrenotazioneComponent } from './my-self-mobile-prenotazione/my-self-mobile-prenotazione.component';
import { MySelfMobilePrenotazioniComponent } from './my-self-mobile-prenotazioni/my-self-mobile-prenotazioni.component';
import { MySelfMobileRecensioniComponent } from './my-self-mobile-recensioni/my-self-mobile-recensioni.component';
import { MySelfMobileRichiesteComponent } from './my-self-mobile-richieste/my-self-mobile-richieste.component';
import {
  MySelfMobileRiepilogoArticoliComponent,
} from './my-self-mobile-riepilogo-articoli/my-self-mobile-riepilogo-articoli.component';
import { MySelfMobileSondaggiComponent } from './my-self-mobile-sondaggi/my-self-mobile-sondaggi.component';
import { MySelfMobileTransazioniComponent } from './my-self-mobile-transazioni/my-self-mobile-transazioni.component';
import { MySelfMobileVoucherComponent } from './my-self-mobile-voucher/my-self-mobile-voucher.component';
import { MySelfMobileComponent } from './my-self-mobile/my-self-mobile.component';

const outletName: string = 'myself';

const routes: Routes = [
    {
        path: "",
        component: MySelfMobileLoginComponent
    },
    {
        path: "home",
        component: MySelfMobileComponent,
        canActivate: [authGuard],
        canActivateChild: [authGuardChild],
        children: [
            { path: "home", component: MySelfMobileHomeComponent, outlet: outletName },
            { path: "prenotazioni", component: MySelfMobilePrenotazioniComponent, outlet: outletName },
            { path: "messaggi", component: MySelfMobileMessaggiComponent, outlet: outletName },
            { path: "transazioni", component: MySelfMobileTransazioniComponent, outlet: outletName },
            { path: "voucher", component: MySelfMobileVoucherComponent, outlet: outletName },
            { path: "recensioni", component: MySelfMobileRecensioniComponent, outlet: outletName },
            { path: "richieste", component: MySelfMobileRichiesteComponent, outlet: outletName },
            { path: "sondaggi", component: MySelfMobileSondaggiComponent, outlet: outletName },
            { path: "cliente", component: MySelfMobileClienteEditComponent, outlet: outletName },
            { path: "menu", component: MySelfMobileMenuComponent, outlet: outletName },
            { path: "prenotazione", component: MySelfMobilePrenotazioneComponent, outlet: outletName },
            { path: "checkin", component: MySelfMobileCheckinComponent, outlet: outletName },
            { path: "pagine", component: MySelfMobilePagineComponent, outlet: outletName },
            { path: "pagina", component: MySelfMobilePaginaComponent, outlet: outletName },
            { path: "contenuto", component: MySelfMobileContenutoComponent, outlet: outletName },
            { path: "evento", component: MySelfMobileEventoComponent, outlet: outletName },
            { path: "articoli", component: MySelfMobileArticoliComponent, outlet: outletName },
            { path: "riepilogo-articoli", component: MySelfMobileRiepilogoArticoliComponent, outlet: outletName },
            { path: "informazioni-struttura", component: MySelfMobileInformazioniStrutturaComponent, outlet: outletName },
            { path: "menu-giorno-select", component: MySelfMobileMenuGiornoSelectComponent, outlet: outletName },
            { path: "menu-piatti-select", component: MySelfMobileMenuPiattiSelectComponent, outlet: outletName },
            { path: "menu-piatti-selected", component: MySelfMobileMenuPiattiSelectedComponent, outlet: outletName },
            { path: "articoli-up-selling-prenotazione-select", component: MySelfMobileArticoliUpSellingPrenotazioneComponent, outlet: outletName },
            { path: "articoli-room-service-prenotazione-select", component: MySelfMobileArticoliRoomServicePrenotazioneComponent, outlet: outletName }
        ]
    }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})

export class MySelfMobileRoutingModule { }