import { Component } from '@angular/core';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import { UtilityService } from 'src/app/shared/services/utility.service';

import { MySelfMobileService } from '../my-self-mobile.service';

@Component({
  selector: 'app-my-self-mobile-transazioni',
  templateUrl: './my-self-mobile-transazioni.component.html',
  styleUrls: ['./my-self-mobile-transazioni.component.scss']
})
export class MySelfMobileTransazioniComponent extends BaseComponent {

  constructor(public mySelfMobileService: MySelfMobileService, public utility: UtilityService) { super(); }

  openUrlPaymail(uid: string) {
    this.utility.openUrlInNewTab(window.location.origin + '/booking/paymail?uid=' + uid);
  }
}