import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import { DialogOptions, OrdinaPiattiMySelfRQ, PiattiXTipoPastoMySelf, PiattoMySelf } from 'src/app/shared/models/dto';
import { CategoriaAllergene, TipoPasto } from 'src/app/shared/models/enum';
import { ApiService } from 'src/app/shared/services/api.service';
import { ContainerService } from 'src/app/shared/services/container.service';

import { MySelfMobileService } from '../my-self-mobile.service';
import {
    MySelfMobileAllergeniPiattoModalComponent,
} from './my-self-mobile-allergeni-piatto-modal/my-self-mobile-allergeni-piatto-modal.component';

@Component({
  selector: 'app-my-self-mobile-menu-piatti-select',
  templateUrl: './my-self-mobile-menu-piatti-select.component.html',
  styleUrls: ['./my-self-mobile-menu-piatti-select.component.scss']
})
export class MySelfMobileMenuPiattiSelectComponent extends BaseComponent implements OnInit {

  public enumTipoPasto = TipoPasto;
  public piattiSelected: PiattiXTipoPastoMySelf[] = [];
  public piattoSelected: boolean = false;

  @ViewChild('riepilogoContainer') riepilogoContainer: ElementRef;

  constructor(public mySelfMobileService: MySelfMobileService,
    private containerService: ContainerService,
    private apiService: ApiService) { super(); }

  ngOnInit() {
    if (this.mySelfMobileService.giornoMenu == null) {
      this.mySelfMobileService.openPage('prenotazioni');
    } else {
      this.mySelfMobileService.showHeaderBackButton();
      this.mySelfMobileService.setFooterHeight(105);

      for (let tipoPasto of this.mySelfMobileService.giornoMenu.tipiPasto) {
        let t = new PiattiXTipoPastoMySelf();
        t.tipoPasto = tipoPasto.tipoPasto;
        t.piatti = [];
        this.piattiSelected.push(t);
      }
    }
  }

  scrollToRiepilogo() {
    this.riepilogoContainer.nativeElement.scrollIntoView({ behavior: 'smooth' });
  }

  addPiatto(tipoPasto: TipoPasto, piatto: PiattoMySelf) {
    let t = this.piattiSelected.find(x => x.tipoPasto == tipoPasto);
    let p = t.piatti.find(x => x.oid == piatto.oid);

    if (p != null) {
      p.quantita++;
    } else {
      piatto.quantita = 1;
      t.piatti.push(piatto);

      setTimeout(() => {
        this.piattoSelected = true;
      }, 200);
    }
  }

  onPiattoQuantityChange(tipoPasto: TipoPasto, index: number, quantity: number) {
    let t = this.piattiSelected.find(x => x.tipoPasto == tipoPasto);

    if (quantity > 0) {
      t.piatti[index].quantita = quantity;
    } else {
      t.piatti.splice(index, 1);
      this.piattoSelected = this.piattiSelected.some(x => x.piatti.length > 0);
    }
  }

  ordinaPiatti() {
    this.setIsLoading(true);

    let rq = new OrdinaPiattiMySelfRQ();
    rq.oidPrenotazione = this.mySelfMobileService.prenotazione.oid;
    rq.data = this.mySelfMobileService.giornoMenu.data;
    rq.piatti = this.piattiSelected;

    this.apiService.ordinaPiattiMySelf(rq).subscribe(() => {
      this.setIsLoading(false);

      this.showInfo('myself-mobile.piattiOrdinati').then(() => {
        this.mySelfMobileService.openPage('prenotazione');
      });
    });
  }

  checkQuantitaMassima(tipoPasto: TipoPasto, piatto: PiattoMySelf): boolean {
    let valid: boolean = true;

    if (piatto.quantitaMassima > 0) {
      valid = piatto.quantita + piatto.quantitaSelected < piatto.quantitaMassima;
    }

    if (valid) {
      let categoria = this.mySelfMobileService.giornoMenu.tipiPasto.find(x => x.tipoPasto == tipoPasto).categoriePiatti.find(x => x.oid == piatto.oidCategoria);

      if (categoria.quantitaMassima > 0) {
        valid = categoria.piatti.reduce((sum, current) => sum + current.quantita + current.quantitaSelected, 0) < categoria.quantitaMassima;
      }
    }

    return valid;
  }

  isScrollToRiepilogoDisabled(): boolean {
    return !this.piattoSelected || this.containerService.container.scrollTop + this.containerService.container.offsetHeight >= this.containerService.container.scrollHeight || (this.riepilogoContainer != null && this.containerService.container.scrollTop >= this.riepilogoContainer.nativeElement.offsetTop);
  }

  openAllergeni(allergeni: CategoriaAllergene[]) {
    let dialogOptions = new DialogOptions();
    dialogOptions.width = '80vw';
    let ref = this.showModalDialog(MySelfMobileAllergeniPiattoModalComponent, dialogOptions);
    let component = <MySelfMobileAllergeniPiattoModalComponent>ref.componentInstance;
    component.allergeni = allergeni;
  }
}