import { Component, OnInit } from '@angular/core';
import { SessionService } from 'src/app/core/session.service';
import { ComponenteXPrenotazione } from 'src/app/shared/models/bo.model';
import { ContainerService } from 'src/app/shared/services/container.service';

import { MySelfMobileService } from '../my-self-mobile.service';

@Component({
  selector: 'app-my-self-mobile-checkin',
  templateUrl: './my-self-mobile-checkin.component.html',
  styleUrls: ['./my-self-mobile-checkin.component.scss']
})
export class MySelfMobileCheckinComponent implements OnInit {

  public ospitiCheckIn: ComponenteXPrenotazione[] = [];

  constructor(public mySelfMobileService: MySelfMobileService, public sessionService: SessionService, private containerService: ContainerService) { }

  ngOnInit() {
    if (this.mySelfMobileService.prenotazione == null) {
      this.mySelfMobileService.openPage('prenotazioni');
    } else {
      this.mySelfMobileService.showHeaderBackButton();
      this.ospitiCheckIn = JSON.parse(JSON.stringify(this.mySelfMobileService.prenotazione.ospiti));
    }
  }

  onCheckInBack(ospiti: ComponenteXPrenotazione[]) {
    this.mySelfMobileService.prenotazione.ospiti = ospiti;
  }

  onStepChange() {
    this.containerService.scrollToTop();
  }
}