import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import * as moment from 'moment';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import { PrenotazioneMySelf } from 'src/app/shared/models/dto';
import { ModalitaSceltaMenuMySelf } from 'src/app/shared/models/enum';

import { MySelfMobileService } from '../my-self-mobile.service';

@Component({
  selector: 'app-my-self-mobile-prenotazione-container',
  templateUrl: './my-self-mobile-prenotazione-container.component.html',
  styleUrls: ['./my-self-mobile-prenotazione-container.component.scss']
})
export class MySelfMobilePrenotazioneContainerComponent extends BaseComponent implements OnInit {

  @Input() prenotazione: PrenotazioneMySelf;
  @Input() isDettaglioPrenotazione: boolean = false;

  @Output() clickEvent: EventEmitter<PrenotazioneMySelf> = new EventEmitter();

  public isCheckInEnabled: boolean;
  public isCheckInOnlineEseguito: boolean;
  public isScegliPiattiVisible: boolean;

  constructor(private mySelfMobileService: MySelfMobileService) { super(); }

  ngOnInit() {
    this.isCheckInEnabled = this.mySelfMobileService.isCheckInEnabled(this.prenotazione);
    this.isCheckInOnlineEseguito = this.prenotazione.ospiti.every(x => x.isCheckInOnlineEseguito);

    if (!this.isDettaglioPrenotazione && this.mySelfMobileService.ubicazioneIstat.isSceltaMenuAttiva) {
      let today = moment().startOf('day');

      if (this.mySelfMobileService.ubicazioneIstat.modalitaSceltaMenuMySelf == ModalitaSceltaMenuMySelf.GiornoSuccessivo) {
        this.isScegliPiattiVisible = moment(this.getDate(this.prenotazione.arrivo)).subtract(1, 'day').isSameOrBefore(today) && moment(this.getDate(this.prenotazione.partenza)).isSameOrAfter(today);
      } else if (this.mySelfMobileService.ubicazioneIstat.modalitaSceltaMenuMySelf == ModalitaSceltaMenuMySelf.TuttiIGiorni) {
        this.isScegliPiattiVisible = moment(this.getDate(this.prenotazione.partenza)).isSameOrAfter(today);
      }
    } else {
      this.isScegliPiattiVisible = false;
    }
  }

  checkin() {
    this.mySelfMobileService.prenotazione = this.prenotazione;
    this.mySelfMobileService.saveScrollPosition();
    this.mySelfMobileService.openPage('checkin');
  }
}