import { Component, OnInit } from '@angular/core';

import { MySelfMobileService } from '../my-self-mobile.service';

@Component({
  selector: 'app-my-self-mobile-articoli',
  templateUrl: './my-self-mobile-articoli.component.html',
  styleUrls: ['./my-self-mobile-articoli.component.scss']
})
export class MySelfMobileArticoliComponent implements OnInit {

  public acquistaEnabled: boolean = false;
  public totaleArticoli: number = 0;

  constructor(public mySelfMobileService: MySelfMobileService) { }

  ngOnInit() {
    if (this.mySelfMobileService.articoliXVoceAddebito == null) {
      this.mySelfMobileService.openPage('home');
    } else {
      this.mySelfMobileService.showHeaderBackButton();
      this.mySelfMobileService.setFooterHeight(105);

      if (this.mySelfMobileService.resetArticoliSelected) {
        this.mySelfMobileService.articoliSelected = JSON.parse(JSON.stringify(this.mySelfMobileService.articoliXVoceAddebito));
        this.mySelfMobileService.resetArticoliSelected = false;
      } else {
        this.calculateTotale();
      }
    }
  }

  acquista() {
    this.mySelfMobileService.saveScrollPosition();
    this.mySelfMobileService.openPage('riepilogo-articoli');
  }

  calculateTotale() {
    this.acquistaEnabled = this.mySelfMobileService.articoliSelected.some(x => x.quantita > 0);

    this.totaleArticoli = 0;

    for (let articolo of this.mySelfMobileService.articoliSelected) {
      if (articolo.quantita > 0) {
        if (articolo.giorni.length > 0) {
          for (let giorno of articolo.giorni) {
            if (giorno.selected) {
              this.totaleArticoli += giorno.prezzo * articolo.quantita;
            }
          }
        } else {
          this.totaleArticoli += articolo.prezzo * articolo.quantita;
        }
      }
    }
  }
}