import { Component, Input, OnInit } from '@angular/core';
import { MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { SessionService } from 'src/app/core/session.service';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import { UbicazioneIstat, UbicazioneIstatXLingua } from 'src/app/shared/models/bo.model';
import { WebApiRequestFilter, WebApiResponseFilter } from 'src/app/shared/models/dto';
import { CriteriaOperator, FilterValueType, TestoMySelf } from 'src/app/shared/models/enum';
import { ApiService } from 'src/app/shared/services/api.service';

import { MySelfMobileService } from '../my-self-mobile.service';

@Component({
  selector: 'app-my-self-mobile-testo-edit',
  templateUrl: './my-self-mobile-testo-edit.component.html',
  styleUrls: ['./my-self-mobile-testo-edit.component.scss']
})
export class MySelfMobileTestoEditComponent extends BaseComponent implements OnInit {

  @Input() testoMySelf: TestoMySelf;
  @Input() dialogRef: MatDialogRef<{}, any>;

  public rq: UbicazioneIstatXLingua[] = [];

  constructor(private apiService: ApiService, public sessionService: SessionService, public mySelfMobileService: MySelfMobileService) {
    super();
  }

  ngOnInit() {
    this.setIsLoading(true);

    for (let lingua of this.lingue) {
      let ubicazioneIstat = new UbicazioneIstatXLingua();
      ubicazioneIstat.lingua = lingua;
      ubicazioneIstat.ubicazioneIstat = new UbicazioneIstat(this.mySelfMobileService.ubicazioneIstat.oid);
      this.rq.push(ubicazioneIstat);
    }

    this.apiService.getUbicazioniIstatXLinguaFilter(WebApiRequestFilter.fromFilter('oid', CriteriaOperator.Equal, this.mySelfMobileService.ubicazioneIstat.oid.toString(), FilterValueType.Number)).subscribe((ubicazioneRS: WebApiResponseFilter) => {
      if (ubicazioneRS.data.length == 1) {
        for (let ubicazioneXLingua of ubicazioneRS.data[0].ubicazioniIstatXLingua) {
          if (ubicazioneXLingua.lingua != null) {
            let index = this.rq.findIndex(x => x.lingua.oid == ubicazioneXLingua.lingua.oid);

            if (index >= 0) {
              this.rq[index].testoIniziale = ubicazioneXLingua.testoIniziale;
              this.rq[index].testoIniziale2 = ubicazioneXLingua.testoIniziale2;
            }
          }
        }
      }

      this.setIsLoading(false);
    });
  }

  onTestoChange(index: number, testo: string) {
    if (this.testoMySelf == TestoMySelf.TestoIniziale) {
      this.rq[index].testoIniziale = testo;
    } else {
      this.rq[index].testoIniziale2 = testo;
    }
  }

  save() {
    this.setIsLoading(true);
    this.apiService.setUbicazioniIstatXLingua(this.rq).subscribe(() => {
      this.dialogRef.close();
      this.mySelfMobileService.downloadEvent.emit();
    });
  }
}