import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import { GiorniSelectComponent } from 'src/app/interfaces/giorni-select/giorni-select.component';
import { ArticoloMySelf, DialogOptions, PrezzoGiornoMySelf } from 'src/app/shared/models/dto';
import { UtilityService } from 'src/app/shared/services/utility.service';

@Component({
  selector: 'app-my-self-mobile-articolo-selected',
  templateUrl: './my-self-mobile-articolo-selected.component.html',
  styleUrls: ['./my-self-mobile-articolo-selected.component.scss']
})
export class MySelfMobileArticoloSelectedComponent extends BaseComponent implements OnInit {

  @Input() articolo: ArticoloMySelf;

  @Output() giorniChangeEvent: EventEmitter<any> = new EventEmitter();

  public totale: number = 0;

  constructor(public utility: UtilityService) { super(); }

  ngOnInit() {
    this.calculateTotale();
  }

  openGiornoSelect() {
    let dialogOptions = new DialogOptions();
    dialogOptions.width = "80vw";

    let ref = this.showModalDialog(GiorniSelectComponent, dialogOptions);
    let component = <GiorniSelectComponent>ref.componentInstance;
    component.giorni = JSON.parse(JSON.stringify(this.articolo.giorni));
    component.dialogRef = ref;

    ref.afterClosed().subscribe((x: PrezzoGiornoMySelf[]) => {
      if (x != null) {
        let i = 0;

        for (let giorno of x) {
          this.articolo.giorni[i].selected = giorno.selected;
          i++;
        }

        this.calculateTotale();
        
        this.giorniChangeEvent.emit();
      }
    });
  }

  calculateTotale() {
    this.totale = this.articolo.giorni.filter(x => x.selected).reduce((sum, current) => sum + (current.prezzo * this.articolo.quantita), 0);
  }
}