import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { ArticoloMySelf } from 'src/app/shared/models/dto';
import { TipoApplicazioneSupplemento, TipoSupplemento } from 'src/app/shared/models/enum';
import { UtilityService } from 'src/app/shared/services/utility.service';

import { MySelfMobileService } from '../my-self-mobile.service';

@Component({
  selector: 'app-my-self-mobile-articolo',
  templateUrl: './my-self-mobile-articolo.component.html',
  styleUrls: ['./my-self-mobile-articolo.component.scss']
})
export class MySelfMobileArticoloComponent implements OnInit {

  @Input() articolo: ArticoloMySelf;

  @Output() quantitaChangeEvent: EventEmitter<any> = new EventEmitter();

  public quantities: number[] = [];
  public quantitaMassima: number;
  public numeroPax: number = 0;
  public enumTipoSupplemento = TipoSupplemento;

  constructor(private mySelfMobileService: MySelfMobileService, public utility: UtilityService) { }

  ngOnInit() {
    this.quantitaMassima = this.articolo.quantitaMassima > 0 ? this.articolo.quantitaMassima : 10;

    if (this.mySelfMobileService.prenotazioniFuture.length == 1) {
      this.numeroPax = this.mySelfMobileService.prenotazioniFuture[0].numeroAdulti + this.mySelfMobileService.prenotazioniFuture[0].numeroBambini;

      if (this.articolo.quantitaMassima <= 0 && this.articolo.isApplicaATuttiComponenti == true) {
        this.quantitaMassima = this.numeroPax * 10;
      }
    }

    this.updateQuantities();
  }

  updateQuantities() {
    this.quantities = [];

    for (let i = 0; i <= (this.articolo.isApplicaATuttiComponenti == true && this.numeroPax > 0 ? Math.ceil(this.quantitaMassima / this.numeroPax) : this.quantitaMassima); i++) {
      this.quantities.push((this.articolo.isApplicaATuttiComponenti == true && this.numeroPax > 0 ? i * this.numeroPax : i) * (this.articolo.giorni.length > 0 ? this.articolo.giorni.filter(x => x.selected).reduce((sum, current) => sum + current.prezzo, 0) : this.articolo.prezzo));
    }
  }

  getQuantity(iQuantity: number): number {
    let isApplicaATuttiComponenti: boolean = this.articolo.isApplicaATuttiComponenti;
    let quantita: number = iQuantity * this.numeroPax;
    return isApplicaATuttiComponenti == true && this.numeroPax > 0 ? Math.min(quantita, this.quantitaMassima) : iQuantity;
  }

  getTipoExtra(): string {
    if (this.articolo.tipo == null) {
      return 'myself.perCameraExtra';
    } else if (this.articolo.tipo == TipoSupplemento.Camera) {
      if (this.articolo.tipoApplicazione == TipoApplicazioneSupplemento.PrimoGiorno || this.articolo.tipoApplicazione == TipoApplicazioneSupplemento.UltimoGiorno) {
        return 'myself.perCameraSingoloGiorno';
      } else {
        return 'myself.perCamera';
      }
    } else if (this.articolo.tipo == TipoSupplemento.Persona) {
      if (this.articolo.tipoApplicazione == TipoApplicazioneSupplemento.PrimoGiorno || this.articolo.tipoApplicazione == TipoApplicazioneSupplemento.UltimoGiorno) {
        return 'myself.perPersonaSingoloGiorno';
      } else {
        return 'myself.perPersona';
      }
    } else {
      return '';
    }
  }
}