import { Component, Input } from '@angular/core';
import { ArticoliXVoceAddebitoMySelf } from 'src/app/shared/models/dto';

import { MySelfMobileService } from '../my-self-mobile.service';

@Component({
  selector: 'app-my-self-mobile-voce-addebito',
  templateUrl: './my-self-mobile-voce-addebito.component.html',
  styleUrls: ['./my-self-mobile-voce-addebito.component.scss']
})
export class MySelfMobileVoceAddebitoComponent {

  @Input() articoliXVoceAddebito: ArticoliXVoceAddebitoMySelf;

  public descrizione: string;

  constructor(private mySelfMobileService: MySelfMobileService) { }

  showArticoli() {
    this.mySelfMobileService.articoliXVoceAddebito = this.articoliXVoceAddebito.articoli;
    this.mySelfMobileService.saveScrollPosition();
    this.mySelfMobileService.openPage('articoli');
  }
}