import { Component, ElementRef, Input, OnInit, ViewChild } from '@angular/core';
import { ArticoliXVoceAddebitoMySelf, ArticoloMySelf } from 'src/app/shared/models/dto';
import { ContainerService } from 'src/app/shared/services/container.service';
import { UtilityService } from 'src/app/shared/services/utility.service';

import { MySelfMobileService } from '../my-self-mobile.service';

@Component({
  selector: 'app-my-self-mobile-articoli-prenotazione',
  templateUrl: './my-self-mobile-articoli-prenotazione.component.html',
  styleUrls: ['./my-self-mobile-articoli-prenotazione.component.scss']
})
export class MySelfMobileArticoliPrenotazioneComponent implements OnInit {

  @Input() isUpSelling: boolean;

  public articoli: ArticoliXVoceAddebitoMySelf[];
  public articoliSelected: ArticoloMySelf[] = [];
  public totaleArticoli: number;
  public articoloSelected: boolean = false;

  @ViewChild('riepilogoContainer') riepilogoContainer: ElementRef;

  constructor(public mySelfMobileService: MySelfMobileService,
    private containerService: ContainerService,
    public utility: UtilityService) { }

  ngOnInit() {
    if (this.mySelfMobileService.prenotazione == null) {
      this.mySelfMobileService.openPage('prenotazioni');
    } else {
      this.mySelfMobileService.showHeaderBackButton();
      this.mySelfMobileService.setFooterHeight(105);

      if (this.isUpSelling) {
        this.articoli = this.mySelfMobileService.prenotazione.articoliUpSelling;
      } else {
        this.articoli = this.mySelfMobileService.prenotazione.articoliRoomService;
      }

      this.mySelfMobileService.prenotazioneSelectedArticoli = this.mySelfMobileService.prenotazione;
    }
  }

  updateArticoliSelected() {
    this.articoliSelected = [].concat(...this.articoli.map(x => x.articoli.filter(x => x.quantita > 0)));
    this.articoliSelected.forEach(x => x.totale = x.quantita * (x.giorni != null ? x.giorni.filter(y => y.selected).reduce((sum, current) => sum + current.prezzo, 0) : x.prezzo));
    this.totaleArticoli = this.articoliSelected.reduce((sum, current) => sum + current.totale, 0);

    setTimeout(() => {
      this.articoloSelected = this.articoliSelected.length > 0;
    }, 200);
  }

  scrollToRiepilogo() {
    this.riepilogoContainer.nativeElement.scrollIntoView({ behavior: 'smooth' });
  }

  isScrollToRiepilogoDisabled(): boolean {
    return !this.articoloSelected || this.containerService.container.scrollTop + this.containerService.container.offsetHeight >= this.containerService.container.scrollHeight || (this.riepilogoContainer != null && this.containerService.container.scrollTop >= this.riepilogoContainer.nativeElement.offsetTop);
  }
}