import { Component, OnInit } from '@angular/core';
import { SessionService } from 'src/app/core/session.service';
import { MittenteMessaggio } from 'src/app/shared/models/enum';
import { ApiService } from 'src/app/shared/services/api.service';

import { MySelfMobileService } from '../my-self-mobile.service';

@Component({
  selector: 'app-my-self-mobile-messaggi',
  templateUrl: './my-self-mobile-messaggi.component.html',
  styleUrls: ['./my-self-mobile-messaggi.component.scss']
})
export class MySelfMobileMessaggiComponent implements OnInit {

  public enumMittente = MittenteMessaggio;

  constructor(public mySelfMobileService: MySelfMobileService, public sessionService: SessionService, private apiService: ApiService) { }

  ngOnInit() {
    this.apiService.impostaMessaggiLettiHotelCliente(this.mySelfMobileService.cliente.oid).subscribe();
    this.mySelfMobileService.numeroMessaggiNonLetti = 0;
  }
}