import { Component, OnInit } from '@angular/core';
import * as moment from 'moment';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import { ArticoloMySelf, GetPrenotazioneDetailsMySelfRQ, GetPrenotazioneDetailsMySelfRS } from 'src/app/shared/models/dto';
import { ApiService } from 'src/app/shared/services/api.service';
import { LocalizationService } from 'src/app/shared/services/localization.service';
import { UtilityService } from 'src/app/shared/services/utility.service';

import { MySelfMobileService } from '../my-self-mobile.service';

@Component({
  selector: 'app-my-self-mobile-prenotazione',
  templateUrl: './my-self-mobile-prenotazione.component.html',
  styleUrls: ['./my-self-mobile-prenotazione.component.scss']
})
export class MySelfMobilePrenotazioneComponent extends BaseComponent implements OnInit {

  public upSellingRichiesto: string;
  public articoli: ArticoloMySelf[];
  public isCheckInEnabled: boolean;

  constructor(public mySelfMobileService: MySelfMobileService,
    private localizationService: LocalizationService,
    private apiService: ApiService,
    public utility: UtilityService) {
    super();
  }

  ngOnInit() {
    if (this.mySelfMobileService.prenotazione == null) {
      this.mySelfMobileService.openPage('prenotazioni');
    } else {
      this.mySelfMobileService.showHeaderBackButton();
      this.mySelfMobileService.setFooterHeight(105);

      this.isCheckInEnabled = this.mySelfMobileService.isCheckInEnabled(this.mySelfMobileService.prenotazione);

      if (!!this.mySelfMobileService.prenotazione.dataUpSelling && !!this.mySelfMobileService.prenotazione.cameraUpSelling) {
        this.upSellingRichiesto = this.localizationService.getTranslationSync('myself.upSellingRichiesto', { data: this.getDate(this.mySelfMobileService.prenotazione.dataUpSelling, 'DD/MM/YYYY'), camera: this.mySelfMobileService.prenotazione.cameraUpSelling });
      }

      this.setIsLoading(true);

      let rq = new GetPrenotazioneDetailsMySelfRQ();
      rq.oidPrenotazione = this.mySelfMobileService.prenotazione.oid;
      rq.lingua = this.mySelfMobileService.lingua;

      this.mySelfMobileService.prenotazione.giorniMenu = null;
      this.mySelfMobileService.prenotazione.articoliUpSelling = null;
      this.mySelfMobileService.prenotazione.articoliRoomService = null;
      this.mySelfMobileService.prenotazione.estrattoConto = null;
      this.mySelfMobileService.prenotazione.upSellingRooms = null;
      this.mySelfMobileService.prenotazione.dataUpSelling = null;
      this.mySelfMobileService.prenotazione.cameraUpSelling = null;
      this.articoli = null;

      this.apiService.getPrenotazioneDetailsMySelf(rq).subscribe((rs: GetPrenotazioneDetailsMySelfRS) => {
        this.mySelfMobileService.prenotazione.giorniMenu = rs.data.giorniMenu;
        this.mySelfMobileService.prenotazione.articoliUpSelling = rs.data.articoliUpSelling;
        this.mySelfMobileService.prenotazione.articoliRoomService = rs.data.articoliRoomService;
        this.mySelfMobileService.prenotazione.estrattoConto = rs.data.estrattoConto;
        this.mySelfMobileService.prenotazione.upSellingRooms = rs.data.upSellingRooms;
        this.mySelfMobileService.prenotazione.dataUpSelling = rs.data.dataUpSelling;
        this.mySelfMobileService.prenotazione.cameraUpSelling = rs.data.cameraUpSelling;
        this.articoli = rs.data.articoli;

        for (let giorno of this.mySelfMobileService.prenotazione.giorniMenu) {
          giorno.piattiSelected = JSON.parse(JSON.stringify(giorno.tipiPasto));

          for (let tipoPasto of giorno.piattiSelected) {
            for (let categoria of tipoPasto.categoriePiatti) {
              categoria.piatti = categoria.piatti.filter(x => x.quantitaSelected > 0);
            }

            tipoPasto.categoriePiatti = tipoPasto.categoriePiatti.filter(x => x.piatti.length > 0);
          }

          giorno.piattiSelected = giorno.piattiSelected.filter(x => x.categoriePiatti.length > 0);

          giorno.tipiPasto.forEach(x => x.categoriePiatti.forEach(y => y.piatti.forEach(z => z.note = null)));
        }

        this.setIsLoading(false);
      });
    }
  }

  checkin() {
    this.mySelfMobileService.saveScrollPosition();
    this.mySelfMobileService.openPage('checkin');
  }

  onRichiediUpSelling(camera: string) {
    this.upSellingRichiesto = this.localizationService.getTranslationSync('myself.upSellingRichiesto', { data: this.getDate(moment(), 'DD/MM/YYYY'), camera: camera });
    this.mySelfMobileService.prenotazione.upSellingRooms = null;
  }

  openMenuGiornoSelect() {
    if (this.mySelfMobileService.prenotazione.giorniMenu != null && this.mySelfMobileService.prenotazione.giorniMenu.length > 0) {
      this.mySelfMobileService.saveScrollPosition();
      this.mySelfMobileService.openPage('menu-giorno-select');
    }
  }

  openArticoliUpSellingSelect() {
    if (this.mySelfMobileService.prenotazione.articoliUpSelling != null && this.mySelfMobileService.prenotazione.articoliUpSelling.length > 0) {
      this.mySelfMobileService.saveScrollPosition();
      this.mySelfMobileService.openPage('articoli-up-selling-prenotazione-select');
    }
  }

  openArticoliRoomServiceSelect() {
    if (this.mySelfMobileService.prenotazione.articoliRoomService != null && this.mySelfMobileService.prenotazione.articoliRoomService.length > 0) {
      this.mySelfMobileService.saveScrollPosition();
      this.mySelfMobileService.openPage('articoli-room-service-prenotazione-select');
    }
  }

  openRecensioni() {
    this.mySelfMobileService.saveScrollPosition();
    this.mySelfMobileService.openPage('recensioni');
  }

  openSondaggio() {
    this.utility.openUrlInNewTab(window.location.origin + '/booking/sondaggio?oidPrenotazione=' + this.mySelfMobileService.prenotazione.oid);
  }
}