import { Component, OnDestroy, OnInit } from '@angular/core';
import { NavigationStart, Router } from '@angular/router';
import { Subscription } from 'rxjs';

import { MySelfMobileService } from '../my-self-mobile.service';

@Component({
  selector: 'app-my-self-mobile-footer',
  templateUrl: './my-self-mobile-footer.component.html',
  styleUrls: ['./my-self-mobile-footer.component.scss']
})
export class MySelfMobileFooterComponent implements OnInit, OnDestroy {

  public sub: Subscription;
  public route: string;

  constructor(public mySelfMobileService: MySelfMobileService, private router: Router) { }

  ngOnInit() {
    this.checkUrl(window.location.href);

    this.sub = this.router.events.subscribe(event => {
      if (event instanceof NavigationStart) {
        this.mySelfMobileService.footerHeight = 65;
        this.mySelfMobileService.showBackButton = false;
        this.checkUrl(event.url);
      }
    });
  }

  checkUrl(url: string) {
    let outletName: string = '(myself:';
    let outletIndex = url.indexOf(outletName);

    if (outletIndex >= 0) {
      let startIndex = outletIndex + outletName.length;

      if (startIndex >= 0) {
        let endIndex: number;

        do {
          endIndex = url.lastIndexOf(')');
        } while (endIndex < startIndex && endIndex != -1);

        if (endIndex >= 0) {
          this.route = url.substring(startIndex, endIndex);
        }
      }
    }
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }
}