import { SelectionModel } from '@angular/cdk/collections';
import { Component, Input, OnInit } from '@angular/core';
import { MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import { Template, UbicazioneIstat } from 'src/app/shared/models/bo.model';
import { GetTemplatesRS, WebApiRequestFilter, WebApiResponse } from 'src/app/shared/models/dto';
import { CriteriaOperator, FilterValueType, TipoTemplate } from 'src/app/shared/models/enum';
import { ApiService } from 'src/app/shared/services/api.service';

import { MySelfMobileService } from '../my-self-mobile.service';

@Component({
  selector: 'app-my-self-mobile-contenuti-edit',
  templateUrl: './my-self-mobile-contenuti-edit.component.html',
  styleUrls: ['./my-self-mobile-contenuti-edit.component.scss']
})
export class MySelfMobileContenutiEditComponent extends BaseComponent implements OnInit {

  @Input() oidUbicazioneIstat: number;
  @Input() dialogRef: MatDialogRef<{}, any>;

  public rq: UbicazioneIstat = new UbicazioneIstat();

  public selectionContenuti = new SelectionModel<number>(true, []);
  public contenuti: Template[];

  constructor(private apiService: ApiService, private mySelfMobileService: MySelfMobileService) {
    super();
  }

  ngOnInit() {
    this.setIsLoading(true);

    this.apiService.getUbicazioneIstat(this.oidUbicazioneIstat).subscribe((res: WebApiResponse) => {
      this.rq = res.data;

      this.apiService.getTemplatesFilter(WebApiRequestFilter.fromFilter('tipoTemplate', CriteriaOperator.Equal, TipoTemplate.PrenotazioneContenuto.toString(), FilterValueType.Number)).subscribe((templatesRS: GetTemplatesRS) => {
        this.contenuti = templatesRS.data.templates.map(x => x.template);

        this.selectionContenuti = new SelectionModel<number>(true, []);

        if (!!this.rq.oidContenuti) {
          let oidContenuti = this.rq.oidContenuti.split(';');

          for (let oid of oidContenuti) {
            if (oid != '' && !isNaN(+oid) && this.contenuti.findIndex(x => x.oid == +oid) != -1) {
              this.selectionContenuti.toggle(+oid);
            }
          }
        }

        this.setIsLoading(false);
      });
    });
  }

  save() {
    this.setIsLoading(true);

    this.rq.oidContenuti = this.selectionContenuti.selected.toString().replace(/,/g, ';');

    this.apiService.updateUbicazioneIstat(this.rq).subscribe(() => {
      this.mySelfMobileService.downloadEvent.emit();
      this.dialogRef.close();
    });
  }

  isAllSelectedContenuti() {
    const numSelected = this.selectionContenuti.selected.length;
    const numRows = this.contenuti.length;
    return numSelected == numRows;
  }

  masterToggleContenuti() {
    this.isAllSelectedContenuti() ? this.selectionContenuti.clear() : this.contenuti.forEach(row => this.selectionContenuti.select(row.oid));
  }
}