import { Component, OnDestroy, OnInit } from '@angular/core';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import { ArticoloMySelf, GetArticoliMySelfRQ, WebApiResponse } from 'src/app/shared/models/dto';
import { ApiService } from 'src/app/shared/services/api.service';

import { MySelfMobileService } from '../my-self-mobile.service';

@Component({
  selector: 'app-my-self-mobile-riepilogo-articoli',
  templateUrl: './my-self-mobile-riepilogo-articoli.component.html',
  styleUrls: ['./my-self-mobile-riepilogo-articoli.component.scss']
})
export class MySelfMobileRiepilogoArticoliComponent extends BaseComponent implements OnInit, OnDestroy {

  constructor(public mySelfMobileService: MySelfMobileService, private apiService: ApiService) { super(); }

  ngOnInit() {
    if (this.mySelfMobileService.articoliSelected == null) {
      this.mySelfMobileService.openPage('home');
    } else {
      this.mySelfMobileService.showHeaderBackButton();

      if (!this.mySelfMobileService.ubicazioneIstat.isRichiediPagamentoArticoliECommerce) {
        this.mySelfMobileService.setFooterHeight(105);
      } else {
        this.mySelfMobileService.isVisualizzaFormPagamento = true;
      }

      this.mySelfMobileService.articoliSelectedPrenotazione = JSON.parse(JSON.stringify(this.mySelfMobileService.articoliSelected.filter(x => x.quantita > 0)));

      this.mySelfMobileService.prenotazioneSelectedArticoli = this.mySelfMobileService.prenotazioniFuture[0];

      this.mySelfMobileService.totaleArticoli = 0;

      this.onPrenotazioneChange();
    }
  }

  onPrenotazioneChange() {
    this.setIsLoading(true);

    let rq = new GetArticoliMySelfRQ();
    rq.oidPrenotazione = this.mySelfMobileService.prenotazioneSelectedArticoli.oid;
    rq.oidArticoli = this.mySelfMobileService.articoliSelectedPrenotazione.map(x => x.oid);
    rq.lingua = this.mySelfMobileService.lingua;

    this.apiService.getArticoliMySelf(rq).subscribe((rs: WebApiResponse) => {
      let numeroPax: number = this.mySelfMobileService.prenotazioneSelectedArticoli.numeroAdulti + this.mySelfMobileService.prenotazioneSelectedArticoli.numeroBambini;

      let i = 0;

      for (let articolo of rs.data as ArticoloMySelf[]) {
        let quantita = this.mySelfMobileService.articoliSelectedPrenotazione[i].quantita;
        articolo.quantita = articolo.isApplicaATuttiComponenti == true && this.mySelfMobileService.prenotazioniFuture.length > 1 ? (quantita * numeroPax) : quantita;
        i++;
      }

      this.mySelfMobileService.articoliSelectedPrenotazione = rs.data;
      
      this.calculateTotale();
      this.setIsLoading(false);
    });
  }

  calculateTotale() {
    this.mySelfMobileService.totaleArticoli = 0;

    for (let articolo of this.mySelfMobileService.articoliSelectedPrenotazione) {
      if (articolo.quantita > 0) {
        for (let giorno of articolo.giorni) {
          if (giorno.selected) {
            this.mySelfMobileService.totaleArticoli += giorno.prezzo * articolo.quantita;
          }
        }
      }
    }
  }

  ngOnDestroy() {
    this.mySelfMobileService.isVisualizzaFormPagamento = false;
  }
}