import { Component, OnInit } from '@angular/core';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import { GiornoMenuMySelf } from 'src/app/shared/models/dto';

import { MySelfMobileService } from '../my-self-mobile.service';

@Component({
  selector: 'app-my-self-mobile-menu-giorno-select',
  templateUrl: './my-self-mobile-menu-giorno-select.component.html',
  styleUrls: ['./my-self-mobile-menu-giorno-select.component.scss']
})
export class MySelfMobileMenuGiornoSelectComponent extends BaseComponent implements OnInit {

  constructor(public mySelfMobileService: MySelfMobileService) { super(); }

  ngOnInit() {
    if (this.mySelfMobileService.prenotazione == null) {
      this.mySelfMobileService.openPage('prenotazioni');
    } else {
      this.mySelfMobileService.showHeaderBackButton();
    }
  }

  openMenuPiattiSelect(giornoMenu: GiornoMenuMySelf) {
    this.mySelfMobileService.giornoMenu = giornoMenu;
    this.mySelfMobileService.saveScrollPosition();
    this.mySelfMobileService.openPage('menu-piatti-select');
  }

  openMenuPiattiSelected(giornoMenu: GiornoMenuMySelf) {
    this.mySelfMobileService.giornoMenu = giornoMenu;
    this.mySelfMobileService.saveScrollPosition();
    this.mySelfMobileService.openPage('menu-piatti-selected');
  }
}