import { Component, Input, OnInit } from '@angular/core';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import { ContenutoMySelf, DialogOptions } from 'src/app/shared/models/dto';
import clip from 'text-clipper';

import {
  MySelfMobileContenutoEditComponent,
} from '../my-self-mobile-contenuto-edit/my-self-mobile-contenuto-edit.component';
import { MySelfMobileService } from '../my-self-mobile.service';

@Component({
  selector: 'app-my-self-mobile-contenuto',
  templateUrl: './my-self-mobile-contenuto.component.html',
  styleUrls: ['./my-self-mobile-contenuto.component.scss']
})
export class MySelfMobileContenutoComponent extends BaseComponent implements OnInit {

  @Input() contenuto: ContenutoMySelf;
  @Input() full: boolean = true;

  public testo: string;

  constructor(public mySelfMobileService: MySelfMobileService) { super(); }

  ngOnInit() {
    if (this.full) {
      if (this.mySelfMobileService.contenuto == null) {
        this.mySelfMobileService.openPage('home');
      } else {
        this.mySelfMobileService.showHeaderBackButton();
      }
    }

    if (this.contenuto == null) {
      this.contenuto = this.mySelfMobileService.contenuto;
    }

    if (this.contenuto != null && !!this.contenuto.testo) {
      if (this.full) {
        this.testo = this.contenuto.testo
      } else {
        this.testo = clip(this.contenuto.testo, 150, { html: true, breakWords: true });
      }
    }
  }

  openDetails() {
    this.mySelfMobileService.contenuto = this.contenuto;
    this.mySelfMobileService.saveScrollPosition();
    this.mySelfMobileService.openPage('contenuto');
  }

  editContenuto() {
    let dialogOptions = new DialogOptions();
    dialogOptions.width = '80vw';
    dialogOptions.minHeight = '30vh';
    dialogOptions.panelClass = 'myself-mobile-dialog';
    dialogOptions.disableClose = true;

    let ref = this.showModalDialog(MySelfMobileContenutoEditComponent, dialogOptions);
    let component = <MySelfMobileContenutoEditComponent>ref.componentInstance;
    component.oidContenuto = this.contenuto.oid;
    component.dialogRef = ref;
  }
}