import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import { GiorniSelectComponent } from 'src/app/interfaces/giorni-select/giorni-select.component';
import { ArticoloMySelf, DialogOptions, PrezzoGiornoMySelf } from 'src/app/shared/models/dto';
import { TipoApplicazioneSupplemento, TipoSupplemento } from 'src/app/shared/models/enum';
import { UtilityService } from 'src/app/shared/services/utility.service';

import { MySelfMobileService } from '../my-self-mobile.service';

@Component({
  selector: 'app-my-self-mobile-articolo-prenotazione',
  templateUrl: './my-self-mobile-articolo-prenotazione.component.html',
  styleUrls: ['./my-self-mobile-articolo-prenotazione.component.scss']
})
export class MySelfMobileArticoloPrenotazioneComponent extends BaseComponent implements OnInit {

  @Input() articolo: ArticoloMySelf;

  @Output() quantitaGiorniChangeEvent: EventEmitter<any> = new EventEmitter();

  public quantities: number[] = [];
  public quantitaMassima: number;
  public numeroPax: number;

  constructor(public utility: UtilityService, private mySelfMobileService: MySelfMobileService) { super(); }

  ngOnInit() {
    this.numeroPax = this.mySelfMobileService.prenotazione.numeroAdulti + this.mySelfMobileService.prenotazione.numeroBambini;
    this.quantitaMassima = this.articolo.quantitaMassima > 0 ? this.articolo.quantitaMassima : this.articolo.isApplicaATuttiComponenti == true ? this.numeroPax * 10 : 10;
    this.updateQuantities();
  }

  openGiornoSelect() {
    let dialogOptions = new DialogOptions();
    dialogOptions.width = "80vw";

    let ref = this.showModalDialog(GiorniSelectComponent, dialogOptions);
    let component = <GiorniSelectComponent>ref.componentInstance;
    component.giorni = JSON.parse(JSON.stringify(this.articolo.giorni));
    component.dialogRef = ref;

    ref.afterClosed().subscribe((x: PrezzoGiornoMySelf[]) => {
      if (x != null) {
        let i = 0;

        for (let giorno of x) {
          this.articolo.giorni[i].selected = giorno.selected;
          i++;
        }

        this.updateQuantities();

        if (this.articolo.quantita > 0) {
          this.quantitaGiorniChangeEvent.emit();
        }
      }
    });
  }

  updateQuantities() {
    this.quantities = [];

    for (let i = 0; i <= (this.articolo.isApplicaATuttiComponenti == true ? Math.ceil(this.quantitaMassima / this.numeroPax) : this.quantitaMassima); i++) {
      this.quantities.push((this.articolo.isApplicaATuttiComponenti == true ? i * this.numeroPax : i) * this.articolo.giorni.filter(x => x.selected).reduce((sum, current) => sum + current.prezzo, 0));
    }
  }

  getQuantity(iQuantity: number): number {
    let isApplicaATuttiComponenti: boolean = this.articolo.isApplicaATuttiComponenti;
    let quantita: number = iQuantity * this.numeroPax;
    return isApplicaATuttiComponenti == true ? Math.min(quantita, this.quantitaMassima) : iQuantity;
  }

  getTipoExtra(): string {
    if (this.articolo.tipo == null) {
      return 'myself.perCameraExtra';
    } else if (this.articolo.tipo == TipoSupplemento.Camera) {
      if (this.articolo.tipoApplicazione == TipoApplicazioneSupplemento.PrimoGiorno || this.articolo.tipoApplicazione == TipoApplicazioneSupplemento.UltimoGiorno) {
        return 'myself.perCameraSingoloGiorno';
      } else {
        return 'myself.perCamera';
      }
    } else if (this.articolo.tipo == TipoSupplemento.Persona) {
      if (this.articolo.tipoApplicazione == TipoApplicazioneSupplemento.PrimoGiorno || this.articolo.tipoApplicazione == TipoApplicazioneSupplemento.UltimoGiorno) {
        return 'myself.perPersonaSingoloGiorno';
      } else {
        return 'myself.perPersona';
      }
    } else {
      return '';
    }
  }
}