import { AfterViewInit, Component, OnDestroy } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import * as moment from 'moment';
import { DeviceDetectorService } from 'ngx-device-detector';
import { Subscription } from 'rxjs';
import { SessionService } from 'src/app/core/session.service';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import { Lingua } from 'src/app/shared/models/bo.model';
import { DialogOptions, GetMySelfRQ, GetMySelfRS, WebApiResponse } from 'src/app/shared/models/dto';
import { MittenteMessaggio, Pagina } from 'src/app/shared/models/enum';
import { ApiService } from 'src/app/shared/services/api.service';
import { ContainerService } from 'src/app/shared/services/container.service';
import { CssLoaderService } from 'src/app/shared/services/css-loader.service';
import { LocalizationService } from 'src/app/shared/services/localization.service';
import { UtilityService } from 'src/app/shared/services/utility.service';

import { MySelfMobileArticoliEditComponent } from '../my-self-mobile-articoli-edit/my-self-mobile-articoli-edit.component';
import {
    MySelfMobileContenutiEditComponent,
} from '../my-self-mobile-contenuti-edit/my-self-mobile-contenuti-edit.component';
import { MySelfMobileService } from '../my-self-mobile.service';

@Component({
  selector: 'app-my-self-mobile',
  templateUrl: './my-self-mobile.component.html',
  styleUrls: ['./my-self-mobile.component.scss']
})
export class MySelfMobileComponent extends BaseComponent implements AfterViewInit, OnDestroy {

  public initialized: boolean = false;
  public sub: Subscription;
  public sub1: Subscription;
  public sub2: Subscription;
  public rq = new GetMySelfRQ();
  public configPanelVisible: boolean = true;
  public setColorTimeout: NodeJS.Timeout;
  public noPaymentMethod: boolean = false;

  constructor(public apiService: ApiService,
    private containerService: ContainerService,
    private deviceService: DeviceDetectorService,
    public sessionService: SessionService,
    private localizationService: LocalizationService,
    public mySelfMobileService: MySelfMobileService,
    private cssLoaderService: CssLoaderService,
    private utility: UtilityService,
    private router: Router,
    private route: ActivatedRoute) {
    super();
  }

  ngAfterViewInit() {
    this.sessionService.isAttachRequestHeaders = true;

    this.containerService.setScrollingContainer('myself-mobile-scrolling', null, false);

    this.sub = this.route.queryParamMap.subscribe(x => {
      let oidUbicazioneIstat = x.get('oidUbicazioneIstat');

      if (!!oidUbicazioneIstat && !isNaN(+oidUbicazioneIstat)) {
        this.mySelfMobileService.config = x.get('mode') == 'config';

        this.rq.oidAlbergo = this.sessionService.getOidAlbergo();
        this.rq.oidUbicazioneIstat = +oidUbicazioneIstat;
        this.rq.oidCliente = this.sessionService.getOidAnagraficaHotel();
        this.rq.all = false;
        this.rq.isMobile = this.deviceService.isMobile();
        this.rq.lingua = x.get('lingua');
        this.rq.isMySelf = true;

        this.download();
      } else {
        this.router.navigate(['sbm']);
      }
    });

    this.sub1 = this.mySelfMobileService.allClickEvent.subscribe((all: boolean) => {
      this.rq.all = all;
      this.download();
    });

    this.sub2 = this.mySelfMobileService.downloadEvent.subscribe(() => {
      this.download();
    });
  }

  download() {
    this.setIsLoading(true);

    this.apiService.getMySelf(this.rq).subscribe((res: GetMySelfRS) => {
      if (res.data.ubicazioneIstat != null) {
        this.cssLoaderService.load(this.sessionService.getOidAlbergo(), Pagina.MySelfMobile, res.data.ubicazioneIstat.oid);

        if (res.data.portale != null) {
          this.mySelfMobileService.portale = res.data.portale;
        }

        this.mySelfMobileService.ubicazioneIstat = res.data.ubicazioneIstat;

        this.cssLoaderService.setColor(this.mySelfMobileService.ubicazioneIstat.coloreMySelf);

        this.mySelfMobileService.prenotazioni = res.data.prenotazioni;
        this.mySelfMobileService.prenotazioniFuture = res.data.prenotazioni.filter(x => moment(this.getDate(x.arrivo)).isSameOrAfter(moment().startOf('day')));
        this.mySelfMobileService.prenotazioneFutura = res.data.prenotazione;
        this.mySelfMobileService.messaggi = res.data.messaggi;
        this.mySelfMobileService.transazioni = res.data.transazioni;
        this.mySelfMobileService.richieste = res.data.richieste;
        this.mySelfMobileService.voucher = res.data.voucher;
        this.mySelfMobileService.recensioni = res.data.recensioni;
        this.mySelfMobileService.sondaggi = res.data.sondaggi;
        this.mySelfMobileService.pagine = res.data.pagine;
        this.mySelfMobileService.pagineHome = res.data.pagine.filter(x => x.isHome);
        this.mySelfMobileService.cliente = res.data.cliente;
        this.mySelfMobileService.lingue = res.data.lingue;
        this.mySelfMobileService.selectedLanguage = res.data.lingua;
        this.mySelfMobileService.contenuti = res.data.contenuti;
        this.mySelfMobileService.eventi = res.data.eventi;
        this.mySelfMobileService.articoli = res.data.articoli;
        this.mySelfMobileService.modalitaPagamento = res.data.modalitaPagamento;
        this.mySelfMobileService.numeroMessaggiNonLetti = this.mySelfMobileService.messaggi.filter(x => !x.isVisualizzato && x.mittente == MittenteMessaggio.Hotel).length;

        this.utility.setLingua(this.mySelfMobileService.selectedLanguage?.codice).then((lingua: Lingua) => {
          this.mySelfMobileService.lingua = lingua?.codice;

          if (!!res.data.traduzioni) {
            let json;

            try {
              json = JSON.parse(res.data.traduzioni);
            } catch (e) { }

            if (!!json) {
              this.localizationService.setTranslation(this.localizationService.getCurrentLocale(), json);
            }
          }

          this.initialized = true;

          this.containerService.scrollToTop();

          this.setIsLoading(false);
        });
      } else {
        this.router.navigate(['sbm']).then(() => this.setIsLoading(false));
      }
    });
  }

  onUbicazioneIstatChange(oid: number) {
    let url: string = window.location.href;

    let index = url.indexOf('oidUbicazioneIstat=');

    if (index >= 0) {
      let equalIndex = index + 'oidUbicazioneIstat='.length;
      let oidUbicazioneIstatEndIndex = url.indexOf('&', equalIndex);

      if (oidUbicazioneIstatEndIndex >= 0) {
        window.location.href = url.substring(0, equalIndex) + oid + url.substring(oidUbicazioneIstatEndIndex);
      } else {
        window.location.href = url.substring(0, equalIndex) + oid;
      }
    }
  }

  onColoreChange() {
    this.setIsLoading(true);

    this.apiService.getUbicazioneIstat(this.mySelfMobileService.ubicazioneIstat.oid).subscribe((res: WebApiResponse) => {
      if (res.data != null) {
        if (!!this.mySelfMobileService.ubicazioneIstat.coloreMySelf) {
          try {
            res.data.coloreMySelf = this.mySelfMobileService.ubicazioneIstat.coloreMySelf;
          } catch (e) { }
        } else {
          res.data.coloreMySelf = null;
        }

        this.apiService.updateUbicazioneIstat(res.data).subscribe(() => {
          this.cssLoaderService.setColor(this.mySelfMobileService.ubicazioneIstat.coloreMySelf);
          this.setIsLoading(false);
        });
      } else {
        this.setIsLoading(false);
      }
    });
  }

  setColor() {
    if (this.setColorTimeout == null) {
      this.setColorTimeout = setTimeout(() => {
        this.cssLoaderService.setColor(this.mySelfMobileService.ubicazioneIstat.coloreMySelf);
        this.setColorTimeout = null;
      }, 50);
    }
  }

  editArticoli() {
    let dialogOptions = new DialogOptions();
    dialogOptions.width = '80vw';
    dialogOptions.minHeight = '30vh';
    dialogOptions.panelClass = 'myself-mobile-dialog';

    let ref = this.showModalDialog(MySelfMobileArticoliEditComponent, dialogOptions);
    let component = <MySelfMobileArticoliEditComponent>ref.componentInstance;
    component.oidUbicazioneIstat = this.mySelfMobileService.ubicazioneIstat.oid;
    component.dialogRef = ref;

    ref.afterClosed().subscribe(x => {
      if (x) {
        this.mySelfMobileService.openPage('home');
      }
    });
  }

  editContenuti() {
    let dialogOptions = new DialogOptions();
    dialogOptions.width = '80vw';
    dialogOptions.minHeight = '30vh';
    dialogOptions.panelClass = 'myself-mobile-dialog';

    let ref = this.showModalDialog(MySelfMobileContenutiEditComponent, dialogOptions);
    let component = <MySelfMobileContenutiEditComponent>ref.componentInstance;
    component.oidUbicazioneIstat = this.mySelfMobileService.ubicazioneIstat.oid;
    component.dialogRef = ref;
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
    this.sub1.unsubscribe();
    this.sub2.unsubscribe();
  }
}