import { Component, OnInit } from '@angular/core';
import { SessionService } from 'src/app/core/session.service';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import { LocalizationService } from 'src/app/shared/services/localization.service';
import { UtilityService } from 'src/app/shared/services/utility.service';

import { MySelfMobileService } from '../my-self-mobile.service';

@Component({
  selector: 'app-my-self-mobile-header',
  templateUrl: './my-self-mobile-header.component.html',
  styleUrls: ['./my-self-mobile-header.component.scss']
})
export class MySelfMobileHeaderComponent extends BaseComponent implements OnInit {

  public nomeCliente: string;

  constructor(private sessionService: SessionService,
    public mySelfMobileService: MySelfMobileService,
    public utility: UtilityService,
    private localizationService: LocalizationService) {
    super();
  }

  ngOnInit() {
    if (this.mySelfMobileService.cliente != null) {
      this.nomeCliente = (!!this.mySelfMobileService.cliente.nome ? this.mySelfMobileService.cliente.nome[0].toUpperCase() : '') + (!!this.mySelfMobileService.cliente.cognome ? this.mySelfMobileService.cliente.cognome[0].toUpperCase() : '');
    }
  }

  logout() {
    this.showDialogConfirm('myself.effettuareLogout')
      .then(si => {
        this.setIsLoading(true);
        this.sessionService.clearSessione();
        window.location.href = '/myself?oidUbicazioneIstat=' + this.mySelfMobileService.ubicazioneIstat.oid + '&lingua=' + this.localizationService.currentLanguage.codice;
      },
        no => { }
      );
  }
}