import { Location } from '@angular/common';
import { EventEmitter, Injectable } from '@angular/core';
import { Router } from '@angular/router';
import * as moment from 'moment';

import { ImageEditComponent } from '../interfaces/image-edit/image-edit.component';
import { Cliente, Lingua } from '../shared/models/bo.model';
import {
  ArticoliXVoceAddebitoMySelf,
  ArticoloMySelf,
  ContenutoMySelf,
  CreaPrenotazioneExtraRQ,
  CreaPrenotazioneExtraRS,
  DialogOptions,
  EventoMySelf,
  GiornoMenuMySelf,
  MessaggioMySelf,
  PagamentoMySelf,
  PaginaMySelf,
  PaymentModel,
  PortaleMySelf,
  PrenotazioneMySelf,
  RecensioneMySelf,
  RichiestaMySelf,
  SetImmagineUbicazioneIstatRQ,
  SetImmagineUbicazioneIstatRS,
  SondaggioMySelf,
  TransazioneMySelf,
  UbicazioneIstatMySelf,
  VoucherMySelf,
} from '../shared/models/dto';
import { TestoMySelf, TipoImmagineUbicazioneIstat } from '../shared/models/enum';
import { ApiService } from '../shared/services/api.service';
import { ContainerService } from '../shared/services/container.service';
import { UtilityService } from '../shared/services/utility.service';
import { MySelfMobileTestoEditComponent } from './my-self-mobile-testo-edit/my-self-mobile-testo-edit.component';

@Injectable({
  providedIn: 'root'
})
export class MySelfMobileService {

  public portale: PortaleMySelf = new PortaleMySelf();
  public ubicazioneIstat: UbicazioneIstatMySelf = new UbicazioneIstatMySelf();
  public prenotazione: PrenotazioneMySelf;
  public prenotazioni: PrenotazioneMySelf[] = [];
  public prenotazioniFuture: PrenotazioneMySelf[] = [];
  public prenotazioneFutura: PrenotazioneMySelf;
  public messaggi: MessaggioMySelf[] = [];
  public transazioni: TransazioneMySelf[] = [];
  public richieste: RichiestaMySelf[] = [];
  public voucher: VoucherMySelf[] = [];
  public recensioni: RecensioneMySelf[] = [];
  public sondaggi: SondaggioMySelf[] = [];
  public pagina: PaginaMySelf;
  public pagine: PaginaMySelf[] = [];
  public pagineHome: PaginaMySelf[] = [];
  public cliente: Cliente;
  public lingua: string;
  public selectedLanguage: Lingua = new Lingua();
  public lingue: Lingua[] = [];
  public headerHeight: number = 65;
  public footerHeight: number = 65;
  public showBackButton: boolean = false;
  public scrollPositions: number[] = [];
  public config: boolean = false;
  public contenuto: ContenutoMySelf;
  public contenuti: ContenutoMySelf[] = [];
  public evento: EventoMySelf;
  public eventi: EventoMySelf[] = [];
  public articoli: ArticoliXVoceAddebitoMySelf[];
  public articoliXVoceAddebito: ArticoloMySelf[];
  public articoliSelected: ArticoloMySelf[];
  public articoliSelectedPrenotazione: ArticoloMySelf[];
  public prenotazioneSelectedArticoli: PrenotazioneMySelf;
  public modalitaPagamento: PagamentoMySelf[];
  public enumTestoMySelf = TestoMySelf;
  public enumTipoImmagineUbicazioneIstat = TipoImmagineUbicazioneIstat;
  public resetArticoliSelected: boolean = true;
  public isVisualizzaFormPagamento: boolean = false;
  public totaleArticoli: number = 0;
  public giornoMenu: GiornoMenuMySelf;
  public numeroMessaggiNonLetti: number = 0;

  public allClickEvent: EventEmitter<boolean> = new EventEmitter();
  public downloadEvent: EventEmitter<any> = new EventEmitter();

  constructor(private router: Router,
    private containerService: ContainerService,
    private utility: UtilityService,
    private apiService: ApiService,
    private location: Location) { }

  openPage(name: string) {
    this.router.navigate(['myself/home', { outlets: { myself: name } }], { queryParamsHandling: 'preserve' }).then(() => {
      this.containerService.scrollToTop(false);
    });
  }

  saveScrollPosition() {
    this.scrollPositions.push(this.containerService.container.scrollTop);
  }

  setHeaderHeight(headerHeight: number) {
    setTimeout(() => {
      this.headerHeight = headerHeight;
    }, 10);
  }

  setFooterHeight(footerHeight: number) {
    setTimeout(() => {
      this.footerHeight = footerHeight;
    }, 10);
  }

  showHeaderBackButton() {
    setTimeout(() => {
      this.showBackButton = true;
    }, 10);
  }

  editTesto(testoMySelf: TestoMySelf) {
    let dialogOptions = new DialogOptions();
    dialogOptions.width = '80vw';
    dialogOptions.minHeight = '30vh';
    dialogOptions.panelClass = 'myself-mobile-dialog';

    let ref = this.utility.showModalDialog(MySelfMobileTestoEditComponent, dialogOptions);
    let component = <MySelfMobileTestoEditComponent>ref.componentInstance;
    component.testoMySelf = testoMySelf;
    component.dialogRef = ref;
  }

  editImmagine(tipoImmagine: TipoImmagineUbicazioneIstat): Promise<boolean> {
    return new Promise<boolean>(resolve => {
      let dialogOptions = new DialogOptions();
      dialogOptions.width = '50vw';
      dialogOptions.panelClass = 'myself-mobile-dialog';

      let ref = this.utility.showModalDialog(ImageEditComponent, dialogOptions);
      let component = <ImageEditComponent>ref.componentInstance;
      component._url = tipoImmagine == TipoImmagineUbicazioneIstat.HeaderMySelf ? this.ubicazioneIstat.urlHeaderMySelf : this.ubicazioneIstat.urlImmagineSfondo;
      component.title = tipoImmagine == TipoImmagineUbicazioneIstat.HeaderMySelf ? 'sbm.configurazione-myself.impostazioni.immagineHeaderMySelf' : 'sbm.configurazione-myself.impostazioni.immagineSfondo';
      component.blue = true;
      component.dialogRef = ref;

      ref.afterClosed().subscribe(base64 => {
        if (base64 != null) {
          this.utility.setIsLoading(true);

          let rq = new SetImmagineUbicazioneIstatRQ();
          rq.oid = this.ubicazioneIstat.oid;
          rq.tipoImmagine = tipoImmagine;
          rq.base64 = base64;

          this.apiService.setImmagineUbicazioneIstat(rq).subscribe((res: SetImmagineUbicazioneIstatRS) => {
            if (tipoImmagine == TipoImmagineUbicazioneIstat.HeaderMySelf) {
              this.ubicazioneIstat.urlHeaderMySelf = res.data.url;
            } else {
              this.ubicazioneIstat.urlImmagineSfondo = res.data.url;
            }

            this.utility.setIsLoading(false);

            resolve(true);
          });
        } else {
          resolve(false);
        }
      });
    });
  }

  acquistaArticoli(articoli: ArticoloMySelf[], isUpSelling: boolean, pagamento?: PaymentModel, redirectToHome: boolean = true) {
    this.utility.setIsLoading(true);

    let rq = new CreaPrenotazioneExtraRQ();
    rq.oidPrenotazione = this.prenotazioneSelectedArticoli.oid;
    rq.articoli = articoli;
    rq.pagamento = pagamento;
    rq.isUpSelling = isUpSelling;

    this.apiService.creaPrenotazioneExtraSelfCheckin(rq).subscribe((res: CreaPrenotazioneExtraRS) => {
      if (res.data.success) {
        this.utility.showInfo('myself.richiestaInviata').then(() => {
          if (redirectToHome) {
            this.openPage('home');
          } else {
            this.back();
          }
        });
      } else {
        this.utility.showError('myself.erroreRichiesta');
      }

      this.utility.setIsLoading(false);
    });
  }

  onPrenotazioneClick(prenotazione: PrenotazioneMySelf) {
    this.prenotazione = prenotazione;
    this.saveScrollPosition();
    this.openPage('prenotazione');
  }

  back() {
    this.location.back();

    setTimeout(() => {
      if (this.scrollPositions.length > 0) {
        let scrollPosition = this.scrollPositions.pop();
        this.containerService.scrollToPosition(scrollPosition, false);
        //this.containerService.scrollToPosition(scrollPosition);
      } else {
        this.containerService.scrollToTop(false);
        //this.containerService.scrollToTop();
      }
    }, 25);
  }

  isCheckInEnabled(prenotazione: PrenotazioneMySelf): boolean {
    return moment(this.utility.getDate(prenotazione.arrivo)).isSameOrAfter(moment().startOf('day'));
  }
}