import { Component, Input, OnInit } from '@angular/core';
import { EventoMySelf } from 'src/app/shared/models/dto';
import clip from 'text-clipper';

import { MySelfMobileService } from '../my-self-mobile.service';

@Component({
  selector: 'app-my-self-mobile-evento',
  templateUrl: './my-self-mobile-evento.component.html',
  styleUrls: ['./my-self-mobile-evento.component.scss']
})
export class MySelfMobileEventoComponent implements OnInit {

  @Input() evento: EventoMySelf;
  @Input() full: boolean = true;

  public descrizione: string;

  constructor(private mySelfMobileService: MySelfMobileService) { }

  ngOnInit() {
    if (this.full) {
      if (this.mySelfMobileService.evento == null) {
        this.mySelfMobileService.openPage('home');
      } else {
        this.mySelfMobileService.showHeaderBackButton();
      }
    }

    if (this.evento == null) {
      this.evento = this.mySelfMobileService.evento;
    }

    if (this.evento != null && !!this.evento.descrizione) {
      if (this.full) {
        this.descrizione = this.evento.descrizione
      } else {
        this.descrizione = clip(this.evento.descrizione, 150, { html: true, breakWords: true });
      }
    }
  }

  openDetails() {
    this.mySelfMobileService.evento = this.evento;
    this.mySelfMobileService.saveScrollPosition();
    this.mySelfMobileService.openPage('evento');
  }
}