import { Component, OnInit } from '@angular/core';
import { LocalStorageService } from 'src/app/core/local-storage.service';
import { SessionService } from 'src/app/core/session.service';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import { Cliente } from 'src/app/shared/models/bo.model';
import { ApiService } from 'src/app/shared/services/api.service';

import { MySelfMobileService } from '../my-self-mobile.service';

@Component({
  selector: 'app-my-self-mobile-cliente-edit',
  templateUrl: './my-self-mobile-cliente-edit.component.html',
  styleUrls: ['./my-self-mobile-cliente-edit.component.scss']
})
export class MySelfMobileClienteEditComponent extends BaseComponent implements OnInit {

  public cliente: Cliente;

  constructor(public mySelfMobileService: MySelfMobileService,
    private localStorage: LocalStorageService,
    private sessionService: SessionService,
    public apiService: ApiService) {
    super();
  }

  ngOnInit() {
    this.mySelfMobileService.setFooterHeight(105);
    this.cliente = JSON.parse(JSON.stringify(this.mySelfMobileService.cliente));
  }

  save() {
    this.setIsLoading(true);

    if (this.cliente.password == null) {
      this.cliente.password = '';
    }

    this.apiService.setCliente(this.cliente).subscribe(() => {
      this.mySelfMobileService.cliente = this.cliente;

      this.sessionService.sessione.clienteMySelf.nome = this.cliente.nome;
      this.sessionService.sessione.clienteMySelf.cognome = this.cliente.cognome;
      this.sessionService.sessione.clienteMySelf.email = this.cliente.email;
      this.sessionService.sessione.clienteMySelf.cellulare = this.cliente.cellulare;
      this.sessionService.sessione.clienteMySelf.password = this.cliente.password;
      this.sessionService.sessione.clienteMySelf.lingua = this.cliente.lingua?.codice;

      this.localStorage.set(this.sessionService.SESSION_KEY, this.sessionService.sessione);

      this.setIsLoading(false);

      this.showInfo('globals.messages.datiSalvati');
    });
  }
}