import { Component, OnDestroy, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { GetMySelfConfigRQ, GetMySelfConfigRS, RecuperaPasswordRQ, RecuperaPasswordRS } from 'src/app/shared/models/dto';
import { ApiService } from 'src/app/shared/services/api.service';
import { LocalizationService } from 'src/app/shared/services/localization.service';
import { UtilityService } from 'src/app/shared/services/utility.service';

import { AuthenticationService } from '../../core/auth/authentication.service';
import { BaseComponent } from '../../interfaces/base-component/base-component';
import { MySelfMobileService } from '../my-self-mobile.service';

@Component({
  selector: 'app-my-self-mobile-login',
  templateUrl: './my-self-mobile-login.component.html',
  styleUrls: ['./my-self-mobile-login.component.scss']
})
export class MySelfMobileLoginComponent extends BaseComponent implements OnInit, OnDestroy {

  public sub: any;
  public oidAlbergo: number;
  public recuperoPassword: boolean = false;
  public emailCodice: string;
  public passwordCognome: string;
  public accessoConCodice: boolean = true;
  public backgroundImage: string;
  public isLoginInCorso: boolean = false;
  public oidUbicazioneIstat: number;
  public config: boolean = false;

  constructor(private _authService: AuthenticationService,
    private route: ActivatedRoute,
    private router: Router,
    private apiService: ApiService,
    private localizationService: LocalizationService,
    private utility: UtilityService,
    public mySelfMobileService: MySelfMobileService) {
    super();
  }

  ngOnInit() {
    this.sub = this.route.queryParamMap.subscribe(x => {
      this.config = x.get('mode') == 'config';
      let oidUbicazioneIstat = x.get('oidUbicazioneIstat');
      let lingua = x.get('lingua');
      let email = x.get('email');
      let codicePrenotazione = x.get('codicePrenotazione');
      let password = x.get('password');
      let cognome = x.get('cognome');

      if (!!email || !!password) {
        this.accessoConCodice = false;
        this.emailCodice = email;
        this.passwordCognome = password;
      } else if (!!codicePrenotazione || !!cognome) {
        this.accessoConCodice = true;
        this.emailCodice = codicePrenotazione;
        this.passwordCognome = cognome;
      }

      if (!!lingua) {
        let l = this.localizationService.languages.find(language => language.codice == lingua);

        if (l != null) {
          this.localizationService.setDefaultLang(lingua);
        }
      }

      if (!!oidUbicazioneIstat && !isNaN(+oidUbicazioneIstat)) {
        this.oidUbicazioneIstat = +oidUbicazioneIstat;

        this.setIsLoading(true);

        let rq = new GetMySelfConfigRQ();
        rq.oidUbicazioneIstat = this.oidUbicazioneIstat;

        this.apiService.getMySelfConfig(rq).subscribe((res: GetMySelfConfigRS) => {
          if (res.data.ubicazioneIstat != null) {
            this.mySelfMobileService.ubicazioneIstat = res.data.ubicazioneIstat;

            this.utility.setBackgroundImage(res.data.ubicazioneIstat.urlImmagineSfondo).then((image: string) => {
              if (!!image) {
                this.backgroundImage = image;
              } else {
                this.backgroundImage = this.utility.getRandomImage();
              }
            });

            this.oidAlbergo = res.data.ubicazioneIstat.oidAlbergo;

            if (!!this.emailCodice && !!this.passwordCognome) {
              this.login();
            } else {
              this.setIsLoading(false);
            }
          } else {
            this.router.navigate(['sbm']).then(() => {
              this.setIsLoading(false);
            });
          }
        });
      } else {
        this.router.navigate(['sbm']);
      }
    });
  }

  login() {
    if (!this.isLoginInCorso) {
      if (!!this.emailCodice && !!this.passwordCognome) {
        let email: string = null;
        let codicePrenotazione: string = null;
        let password: string = null;
        let cognome: string = null;

        if (this.accessoConCodice) {
          codicePrenotazione = this.emailCodice;
          cognome = this.passwordCognome;
        } else {
          email = this.emailCodice;
          password = this.passwordCognome;
        }

        this.setIsLoading(true);

        this.isLoginInCorso = true;

        this._authService.loginMySelf(true, this.oidAlbergo, null, email, password, cognome, codicePrenotazione).then(() => {
          this.isLoginInCorso = false;
          this.redirectToHome();
        }, () => { this.isLoginInCorso = false; });
      } else {
        this.showError('globals.messages.inserireDati');
      }
    }
  }

  recuperaPassword(email: string) {
    if (!!email) {
      this.setIsLoading(true);

      let rq = new RecuperaPasswordRQ();
      rq.email = email;
      rq.oidAlbergo = this.oidAlbergo;

      this.apiService.recuperaPassword(rq).subscribe((res: RecuperaPasswordRS) => {
        this.setIsLoading(false);

        if (res.data.success) {
          this.showInfo('myself.emailInviata').then(() => {
            this.recuperoPassword = false;
          });
        } else {
          this.showError('myself.erroreRecuperoPassword').then(() => {
            this.recuperoPassword = false;
          });
        }
      });
    } else {
      this.showError('globals.messages.inserireDati');
    }
  }

  redirectToHome() {
    let queryParams: any = { queryParams: { oidUbicazioneIstat: this.oidUbicazioneIstat } };

    if (this.config) {
      queryParams.queryParams.mode = 'config';
    }

    this.router.navigate(['myself/home', { outlets: { myself: 'home' } }], queryParams).then(() => this.setIsLoading(false));
  }

  editImmagineSfondo() {
    this.mySelfMobileService.editImmagine(this.mySelfMobileService.enumTipoImmagineUbicazioneIstat.Sfondo).then(x => {
      if (x) {
        this.utility.setBackgroundImage(this.mySelfMobileService.ubicazioneIstat.urlImmagineSfondo).then((image: string) => {
          if (!!image) {
            this.backgroundImage = image;
          } else {
            this.backgroundImage = this.utility.getRandomImage();
          }
        });
      }
    });
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }
}