import { SelectionModel } from '@angular/cdk/collections';
import { Component, Input, OnInit } from '@angular/core';
import { MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import { Articolo, UbicazioneIstat } from 'src/app/shared/models/bo.model';
import { WebApiRequestFilter, WebApiResponse, WebApiResponseFilter } from 'src/app/shared/models/dto';
import { CriteriaOperator } from 'src/app/shared/models/enum';
import { ApiService } from 'src/app/shared/services/api.service';

import { MySelfMobileService } from '../my-self-mobile.service';

@Component({
  selector: 'app-my-self-mobile-articoli-edit',
  templateUrl: './my-self-mobile-articoli-edit.component.html',
  styleUrls: ['./my-self-mobile-articoli-edit.component.scss']
})
export class MySelfMobileArticoliEditComponent extends BaseComponent implements OnInit {

  @Input() oidUbicazioneIstat: number;
  @Input() dialogRef: MatDialogRef<{}, any>;

  public rq: UbicazioneIstat = new UbicazioneIstat();

  public selectionArticoli = new SelectionModel<number>(true, []);
  public articoli: Articolo[];

  constructor(private apiService: ApiService, private mySelfMobileService: MySelfMobileService) {
    super();
  }

  ngOnInit() {
    this.setIsLoading(true);

    this.apiService.getUbicazioneIstat(this.oidUbicazioneIstat).subscribe((res: WebApiResponse) => {
      this.rq = res.data;

      this.apiService.getArticoliFilter(WebApiRequestFilter.fromFilter('isUpSellingCRM', CriteriaOperator.IsTrue)).subscribe((articoliRS: WebApiResponseFilter) => {
        this.articoli = articoliRS.data;

        this.selectionArticoli = new SelectionModel<number>(true, []);

        if (!!this.rq.oidArticoli) {
          let oidArticoli = this.rq.oidArticoli.split(';');

          for (let oid of oidArticoli) {
            if (oid != '' && !isNaN(+oid) && this.articoli.findIndex(x => x.oid == +oid) != -1) {
              this.selectionArticoli.toggle(+oid);
            }
          }
        }

        this.setIsLoading(false);
      });
    });
  }

  save() {
    this.setIsLoading(true);

    this.rq.oidArticoli = this.selectionArticoli.selected.toString().replace(/,/g, ';');

    this.apiService.updateUbicazioneIstat(this.rq).subscribe(() => {
      this.dialogRef.close(true);
      this.mySelfMobileService.downloadEvent.emit();
    });
  }

  isAllSelectedArticoli() {
    const numSelected = this.selectionArticoli.selected.length;
    const numRows = this.articoli.length;
    return numSelected == numRows;
  }

  masterToggleArticoli() {
    this.isAllSelectedArticoli() ? this.selectionArticoli.clear() : this.articoli.forEach(row => this.selectionArticoli.select(row.oid));
  }
}
