import { Component, OnInit } from '@angular/core';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import { CancellaOrdinePiattiMySelfRQ } from 'src/app/shared/models/dto';
import { TipoPasto } from 'src/app/shared/models/enum';
import { ApiService } from 'src/app/shared/services/api.service';

import { MySelfMobileService } from '../my-self-mobile.service';

@Component({
  selector: 'app-my-self-mobile-menu-piatti-selected',
  templateUrl: './my-self-mobile-menu-piatti-selected.component.html',
  styleUrls: ['./my-self-mobile-menu-piatti-selected.component.scss']
})
export class MySelfMobileMenuPiattiSelectedComponent extends BaseComponent implements OnInit {

  public enumTipoPasto = TipoPasto;

  constructor(public mySelfMobileService: MySelfMobileService, private apiService: ApiService) { super(); }

  ngOnInit() {
    if (this.mySelfMobileService.giornoMenu == null) {
      this.mySelfMobileService.openPage('prenotazioni');
    } else {
      this.mySelfMobileService.showHeaderBackButton();
      this.mySelfMobileService.setFooterHeight(105);
    }
  }

  cancellaOrdinePiatti() {
    this.showDialogConfirm('myself-mobile.cancellareOrdine').then(si => {
      this.setIsLoading(true);

      let rq = new CancellaOrdinePiattiMySelfRQ();
      rq.oidOrdiniPiatti = this.mySelfMobileService.giornoMenu.oidOrdiniPiatti;

      this.apiService.cancellaOrdinePiattiMySelf(rq).subscribe(() => this.mySelfMobileService.openPage('prenotazione'));
    }, no => { });
  }
}