import { Component, Input } from '@angular/core';
import { PaginaMySelf } from 'src/app/shared/models/dto';
import { TipoPaginaMySelf } from 'src/app/shared/models/enum';
import { UtilityService } from 'src/app/shared/services/utility.service';

import { MySelfMobileService } from '../my-self-mobile.service';

@Component({
  selector: 'app-my-self-mobile-page',
  templateUrl: './my-self-mobile-page.component.html',
  styleUrls: ['./my-self-mobile-page.component.scss']
})
export class MySelfMobilePageComponent {

  @Input() pagina: PaginaMySelf;

  constructor(public mySelfMobileService: MySelfMobileService, private utility: UtilityService) { }

  onPaginaClick(pagina: PaginaMySelf) {
    if (pagina.tipoPaginaMySelf == TipoPaginaMySelf.Link) {
      this.utility.openUrlInNewTab(pagina.url);
    } else if (pagina.tipoPaginaMySelf == TipoPaginaMySelf.Pagina) {
      this.mySelfMobileService.pagina = pagina;
      this.mySelfMobileService.saveScrollPosition();
      this.mySelfMobileService.openPage('pagina');
    }
  }
}