import { Component, OnInit } from '@angular/core';
import { BaseComponent } from 'src/app/interfaces/base-component/base-component';
import {
  MySelfNuovaRecensioneComponent,
} from 'src/app/my-self-shared/my-self-nuova-recensione/my-self-nuova-recensione.component';
import { DialogOptions } from 'src/app/shared/models/dto';

import { MySelfMobileService } from '../my-self-mobile.service';

@Component({
  selector: 'app-my-self-mobile-recensioni',
  templateUrl: './my-self-mobile-recensioni.component.html',
  styleUrls: ['./my-self-mobile-recensioni.component.scss']
})
export class MySelfMobileRecensioniComponent extends BaseComponent implements OnInit {

  constructor(public mySelfMobileService: MySelfMobileService) { super(); }

  ngOnInit() {
    this.mySelfMobileService.setFooterHeight(105);
  }

  nuovaRecensione() {
    let dialogOptions = new DialogOptions();
    dialogOptions.width = "90vw";
    dialogOptions.panelClass = 'myself-mobile-dialog';

    let ref = this.showModalDialog(MySelfNuovaRecensioneComponent, dialogOptions);
    let component = <MySelfNuovaRecensioneComponent>ref.componentInstance;
    component.cliente = this.mySelfMobileService.cliente;
    component.portale = this.mySelfMobileService.portale;
    component.dialogRef = ref;

    ref.afterClosed().subscribe(x => {
      if (x) {
        this.mySelfMobileService.recensioni.push(x);
      }
    });
  }
}